import React, { Component } from 'react';
import {
  Table,
  TableHead,
  TableBody,
  TableCell,
  TableRow,
  Checkbox,
  Paper,
  Button,
  Fab,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText
} from "@material-ui/core";
import { Fade } from 'react-reveal';
import dummyProducts from './dummyProducts';
import styles from './ProductList.module.css';
import { IoMdTrash, IoMdCreate, IoIosAddCircle } from 'react-icons/io';

class ProductList extends Component {

  state = {
    tableCategories: ['ID', 'Nombre', 'Precio', 'Stock'],
    rows: dummyProducts.map(product => product.id),
    selectedRows: [],
    showDeleteConfirmationDialog: false
  }

  handleRowClicked = (i) => {
    let selectedRows = [...this.state.selectedRows];

    if (i === '0') {
      if (selectedRows.includes(i)) {
        //Unselect ALL rows
        selectedRows = [];
      } else {
        //Select ALL rows
        selectedRows = [i, ...this.state.rows];
      }
    } else {
      if (selectedRows.includes(i)) {
        //Unselect row
        const indexOfElement = selectedRows.indexOf(i);
        selectedRows.splice(indexOfElement, 1);
      }
      else {
        //Select row
        selectedRows.push(i);
      }
    }

    this.setState({ selectedRows });
  }

  renderTableBodyRows = () => {
    return dummyProducts.map(product => {
      return (
        <TableRow
          key={product.id}
          onClick={() => this.handleRowClicked(product.id)}
          classes={{
            selected: styles.rowSelected
          }}
          selected={this.state.selectedRows.includes(product.id)}
        >
          <TableCell padding='checkbox'>
            <Checkbox
              checked={this.state.selectedRows.includes(product.id)}
              color='primary'
            />
          </TableCell>
          <TableCell>{product.id}</TableCell>
          <TableCell>{product.name}</TableCell>
          <TableCell>{product.price}</TableCell>
          <TableCell>{product.stock}</TableCell>
        </TableRow>
      );
    });
  };

  renderTableHeadCells = () => {
    return this.state.tableCategories.map((category, i) => (
      <TableCell key={i}>
        {category}
      </TableCell>
    ));
  };

  handleDeleteElements = () => {
    this.setState({
      showDeleteConfirmationDialog: !this.state.showDeleteConfirmationDialog
    });
  }

  render() {
    console.log(this.state.selectedRows)

    return (
      <>
        <Fade>
          <Paper>
            <Table>
              <TableHead>
                <TableRow
                  key={'0'}
                  onClick={() => this.handleRowClicked('0')}
                  classes={{
                    selected: styles.rowSelected
                  }}
                  selected={this.state.selectedRows.includes('0')}
                  color='primary'
                >
                  <TableCell padding='checkbox'>
                    <Checkbox
                      checked={this.state.selectedRows.includes('0')}
                      color='primary'
                    />
                  </TableCell>

                  {this.renderTableHeadCells()}

                </TableRow>
              </TableHead>
              <TableBody>
                {this.renderTableBodyRows()}
              </TableBody>
            </Table>
          </Paper>
        </Fade>

        {
          <Fade>
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              marginTop: '2rem'
            }}>
              <Fab
                onClick={() => this.props.history.push(`${process.env.PUBLIC_URL}/handle_product`)}
                color='primary'
                size='small'
                style={{
                  marginRight: '1rem'
                }}
                className={styles.addProductButton}
              >
                <IoIosAddCircle
                  style={{
                    fontSize: '1.2rem'
                  }}
                />
              </Fab>

              <div style={{
                display: 'flex'
              }}>
                {
                  this.state.selectedRows.length > 0 &&
                  <Fade right>
                    <Fab
                      onClick={this.handleDeleteElements}
                      color='secondary'
                      size='small'
                      style={{
                        marginRight: '1rem'
                      }}
                    >
                      <IoMdTrash
                        style={{
                          fontSize: '1.2rem'
                        }}
                      />
                    </Fab>
                  </Fade>
                }

                {
                  this.state.selectedRows.length === 1 &&
                  <Fade right>
                    <Fab
                      color='primary'
                      size='small'
                      href={`${process.env.PUBLIC_URL}/handle_product/${this.state.selectedRows[0]}`}
                    >
                      <IoMdCreate
                        style={{
                          fontSize: '1.2rem'
                        }}
                      />
                    </Fab>
                  </Fade>
                }
              </div>
            </div>
          </Fade>
        }

        <Dialog
          open={this.state.showDeleteConfirmationDialog}
          onClose={this.handleDeleteElements}
        >
          <DialogTitle variant='h6'>
            Deseas eliminar estos elementos?
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              Los elementos seleccionados se eliminaran permanentemente
            </DialogContentText>

            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              marginTop: '1.4rem'
            }}>
              <Button
                onClick={() => this.setState({ showDeleteConfirmationDialog: false })}
                color='secondary'
                variant='contained'
              >
                eliminar
          </Button>
              <Button
                onClick={() => this.setState({ showDeleteConfirmationDialog: false })}
                variant='contained'
              >
                cancelar
          </Button>
            </div>
          </DialogContent>
        </Dialog>
      </>
    );
  }
}

export default ProductList;
