import React, { Component } from 'react';

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Navbar from "./Layout/Navbar/Navbar";
import ProductList from "./Components/ProductList/ProductList";
import Login from "./Components/Login/Login";
import HandleProduct from './Components/Admin/HandleProduct/HandleProduct';

class Routes extends Component {
  state = {
    auth: false
  }

  render() {
    return (
      <Router>
        <div
          style={{
            padding: "2rem 4rem"
          }}
        >
          <Navbar />
          <Switch>
            <Route exact path={`${process.env.PUBLIC_URL}/`} component={ProductList} />
            <Route exact path={`${process.env.PUBLIC_URL}/login`} component={Login} />
            <Route
              exact
              path={`${process.env.PUBLIC_URL}/handle_product`}
              render={(props) =>
                <HandleProduct
                  {...props}
                  title='Agregar un nuevo producto'
                  buttonText='Agregar'
                />
              }
            />
            <Route
              exact
              path={`${process.env.PUBLIC_URL}/handle_product/:id`}
              render={(props) =>
                <HandleProduct
                  {...props}
                  title='Editar producto'
                  buttonText='Guardar'
                />
              }
            />
          </Switch>
        </div>
      </Router>
    );
  }
};

export default Routes;
