import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { TextField, Typography, Button } from "@material-ui/core";
import styles from './HandleProduct.module.css';
import dummyProducts from './../../ProductList/dummyProducts';

class HandleProduct extends Component {
    state = {
        productToEdit: ''
    }

    componentWillMount() {

        if (this.props.match.params.id) {
            const productID = this.props.match.params.id;
            const productToEdit = {
                ...dummyProducts.filter(product => {
                    return product.id === productID;
                })[0]
            };

            this.setState({ productToEdit });
        }
    }

    render() {
        const { productToEdit } = this.state;
        console.log(this.props.match.params)

        return (
            <div className={styles.add_item_wrapper}>
                <Typography variant='h6'>{this.props.title}</Typography>

                <TextField
                    required
                    label='Nombre'
                    margin='normal'
                    onChange={(event) => this.setState({ name: event.target.value })}
                    value={productToEdit && productToEdit.name}
                />

                <TextField
                    required
                    label='Precio'
                    margin='normal'
                    onChange={(event) => this.setState({ password: event.target.value })}
                    value={productToEdit && productToEdit.price}
                />

                <TextField
                    label='Stock'
                    margin='normal'
                    onChange={(event) => this.setState({ stock: event.target.value })}
                    value={productToEdit && productToEdit.stock}
                />

                <NavLink to='/' style={{
                    marginTop: '1.6rem',
                    textDecoration: 'none'
                }}>
                    <Button variant="contained" color='primary'>
                        {this.props.buttonText}
                    </Button>
                </NavLink>
            </div>
        );
    }
}

export default HandleProduct;