import React, { Component } from 'react';
import { Link } from "react-router-dom";

import styles from "./Navbar.module.css";

import {
  AppBar,
  Toolbar,
  Button,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
  Typography
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';

import { FiPackage, FiUser } from 'react-icons/fi';

class Navbar extends Component {
  state = {
    drawerOpen: false,
    menuLinks: [
      {
        path: `${process.env.PUBLIC_URL}/`,
        name: 'Productos',
        icon: <FiPackage style={{
          marginRight: '1rem'
        }} />
      },
      {
        path: `${process.env.PUBLIC_URL}/login`,
        name: 'Login',
        icon: <FiUser style={{
          marginRight: '1rem'
        }} />
      }
    ]
  }

  handleDrawer = () => {
    this.setState({ drawerOpen: !this.state.drawerOpen });
  }

  generateRouterLinks = () => {
    return this.state.menuLinks.map((link, i) => {

      return (
        <Link
          key={i}
          className={styles.link}
          to={link.path}
          onClick={this.handleDrawer}>
          <ListItem button>
            <ListItemText style={{
              padding: '.6rem'
            }}>
              {link.icon}
              {link.name}
            </ListItemText>
          </ListItem>
        </Link>
      );
    })
  };


  render() {
    return (
      <div className={styles.Navbar}>
        <AppBar position='static' style={{
          backgroundColor: '#333'
        }}>
          <Toolbar style={{
            justifyContent: 'space-between'
          }}>
            <IconButton
              color='inherit'
              onClick={this.handleDrawer}
              style={{
                outline: 'none'
              }}>
              <MenuIcon />
            </IconButton>

            <Link to={`${process.env.PUBLIC_URL}/`} className={styles.link}>
              <Button className={styles.button}>
                <Typography color='inherit' variant='h6'>
                  Inventario de productos
                </Typography>
              </Button>
            </Link>

            <Link to={`${process.env.PUBLIC_URL}/login`} className={styles.link}>
              <FiUser />
              <Button className={styles.button}>
                Login
              </Button>
            </Link>

          </Toolbar>
        </AppBar>

        <Drawer
          open={this.state.drawerOpen}
          onClose={this.handleDrawer}
          width='30rem'
        >
          <List>
            {this.generateRouterLinks()}
          </List>
        </Drawer>
      </div>
    );
  }
};

export default Navbar;
